import { useSelector } from "react-redux";
import { ListItem } from "../../components/ListItem/ListItem";
import s from "./SideBar.module.scss";
import { authIDSelector } from "../../selectors";

export const SideBar = () => {
    const authID = useSelector(authIDSelector);

    return (
        <nav className={s.nav}>
            <ul>
                <ListItem
                    link={"/profile/" + authID}
                    name='Profle'
                    spriteID='profile'
                />
                <ListItem
                    link='/message'
                    name='Message'
                    spriteID='messages'
                />
                <ListItem
                    link='/users'
                    name='Users'
                    spriteID='users'
                />
                <ListItem
                    link='/music'
                    name='Music'
                    spriteID='music'
                />
                <ListItem
                    link='/photo'
                    name='Photo'
                    spriteID='photo'
                />
            </ul>
        </nav>
    );
};
