import { Input } from "../../../../components/AddMessageBar/AddMessageBar";
import { DialogMessagesArea } from "./DialogMessagesArea/DialogMessagesArea";
import s from "./DialogMessages.module.scss";
import { useSelector } from "react-redux";
import { useAppDispatch } from "../../../../redux/redux";
import { AddMessageAC, AddNewMessageTextAC } from "../../../../redux/message-reducer";
import { DialogUserBar } from "./DialogUserBar/DialogUserBar";
import { messageInfoSelector, newMessageTxtSelector } from "../../../../selectors";

export const DialogMessages = () => {
    const dispatch = useAppDispatch();

    const newMessageTxt = useSelector(newMessageTxtSelector);
    const messageInfo = useSelector(messageInfoSelector);

    const add = () => {
        dispatch(AddMessageAC());
    };
    const onChangeHandler = (value: string) => {
        dispatch(AddNewMessageTextAC(value));
    };
    return (
        <div className={s.DialogMessages}>
            <DialogUserBar />
            <DialogMessagesArea messageInfo={messageInfo} />
            <Input
                className={s.DialogInput}
                newMessageTxt={newMessageTxt}
                placeholder='Напишите сообщение...'
                add={add}
                onChangeHandler={onChangeHandler}
            />
        </div>
    );
};
