import { useSelector } from "react-redux";
import s from "./DialogUserBar.module.scss";
import { RootStoreType } from "../../../../../redux/redux";

export const DialogUserBar = () => {
    const users = useSelector<RootStoreType, string>(
        state => state.messagePage.dialogsUsers[0].name
    );
    const src = useSelector<RootStoreType, string>(state => state.messagePage.dialogsUsers[0].src);
    return (
        <div>
            <div className={s.wrapper}>
                <div className={s.userName}>{users}</div>
                <img
                    className={s.userImg}
                    src={src}
                    alt=''
                />
            </div>
        </div>
    );
};
