import { messageInfoPropsType } from "../../../../../types";
import { DialogMessage } from "./DialogMessage/DialogMessage";
import s from "./DialogMessagesArea.module.scss";

type DialogMessagesArea = {
    messageInfo: messageInfoPropsType[];
};

export const DialogMessagesArea = (props: DialogMessagesArea) => {
    const messages = props.messageInfo.map(el => (
        <DialogMessage
            key={el.id}
            id={el.id}
            message={el.message}
        />
    ));
    return <div className={s.messagesWrapper}>{messages}</div>;
};
